/* Define a CSS variable for the navbar color */
:root {
    --navbar-color: #181918; /* Default color */
}

.nav {
    display: flex;
    justify-content: space-around;
    gap: 220px;
    align-items: center;
    padding: 10px;
    /* margin: 5px; */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    background: var(--navbar-color); 
    /* background-color: red; */
    /* border-radius: 10px; */
}

nav .logo {
    /* background-color: green; */
    width: 100px;
    font-weight: 800;
    font-size: 25px;

}

nav ul {
    display: flex;
    line-height: 40px;
    /* margin-right: 1rem; */
}

nav ul li {
    list-style-type: none;
}

nav ul li a {
    display: block;
    margin: 0 2px;
    padding: 8px 18px;
    border-radius: 10px;
    font-weight: 600;

}

.nav-icon-span {
    font-size: 18px;
    font-weight: 400    ;
    /* color: #cec1c5; */
    color: #f69db7;
}
 
/* 
.nav-icon-span  :active {
    font-size: 20px;
    color: green;
} */
 
.nav-items {
    line-height: 40px;
    /* margin-right: 1rem; */
}

nav ul .nav-links:after {
    content: "";
    display: block;
    height: 3px;
    width: 0;
    background: transparent;
    
    transition: width 0.7s ease, background-color 0.5s ease;
}

nav ul .nav-links:hover:after {
    width: 100%;
    background: #52575d;
    /* background: #1976d2; */
}

.nav-icon:not(:last-child) {
    font-size: 29px;
}
 
@media screen and (max-width: 900px) {
    .nav ul {
        display: none;
    }
    .nav {
        display: block;
        flex-direction: column; 
        align-items: center;
    }
    .nav .logo {
        margin: 0% 30% ;
    }
}


@media (min-width: 900px) and (max-width:1096px) {
    .nav {
        gap: 10px;
        justify-content: space-around;
    }
}